*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: 'open Sans', sans-serif;
}

.navbar{
  display: flex;
  height: 70px;
  box-shadow: 0 5px 20px 2px rgba(0, 0, 0, 0.1);
  position: inherit;
  top: 0;
}

.container{
  width: 1300px;
  margin: 0 auto;
}

.nav_container{
  width: 100%;
  display: flex;
  align-items: center;
  height: 70px;
  justify-content: space-between;
}

.header_container{
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hr_tag{
  display: flex;
  align-items: center;
  margin-top: 20px;
}

.basket{
  display: flex;
  margin-right: 20px;
  position: relative;
}

.profile{
  display: inline;
  position: absolute;
}
.profile_icon{
  display: flex;
  

}
.basket span{
  display: flex;
  position: absolute;
  top: 0;
  margin-left: 15px;
  background-color: rgb(206, 46, 46);
  color: aliceblue;
  padding: 1px;
  border-radius: 100%;
  width: 20px;
  height: 20px;
  justify-content: center;
  align-items: center;
}
.cart_icon{
  width: 20px;
  font-size: 30px;
}

.mbt-30{
  margin: 30px auto ;
}

.row{
display: flex;
width: 100%;
flex-wrap: wrap;
}
.col-3{
  width: 33%;
}

.product{
  padding: 25px;
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
}
.product_img img{
  width: 100%;
  transition: all 0.5s linear;
}
.product_img img:hover{
  transform: scale(1.1);
}



.product_name{
  margin: 10px 0;
  font-weight: 800;
  font-size: 20px;
  text-transform: capitalize;
}
.product_description{
  font-size: 15px;
  margin-bottom: 5px;
}
.actualprice{
  text-decoration: line-through;
}

.col-6{
  width: 50%;
}

.original-price{
  text-align: right;
}
.product_price{
  font-weight: 800;
}

.btn{
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  width: 100%;
  padding: 10px;
  background-color: #197cdf;
  border: none;
  border-radius: 20px;
  color: aliceblue;
  font-weight: 600;

}
.btn:hover{
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  width: 100%;
  padding: 10px;
  background-color: #fefeff;
  border-radius: 20px;
  color: rgb(93, 167, 231);
  font-weight: 600;
  border: 2px solid rgb(93, 167, 231);

}

@media(max-width: 765px){
  .container{
    padding: 0 30px;
  }
  .col-3{
    width: 50%;
  }
  .product_price{
    font-weight: 600;
  }
  .product_name{
    margin: 10px 0;
    font-weight: 600;
    font-size: 20px;
    text-transform: capitalize;
  }
  .btn{
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    width: 100%;
    padding: 10px;
    background-color: #197cdf;
    border: none;
    border-radius: 20px;
    color: aliceblue;
    font-weight: 200;
  
  }
  
}